.additional {
  @include container($mobile-container);
}

.additional__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  border-top: 1px solid #cdcdcd;
}

.additional__item {
  border-bottom: 1px solid #cdcdcd;
  padding-top: 13px;
  padding-bottom: 20px;
}

.additional__title {
  margin-top: 25px;
  margin-bottom: 36px;
}

.additional__link {
  text-decoration: none;
}

.additional__sub-title {
  @include fonts();
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #222222;
  margin: 13px 0;
}

.additional__items {
  margin-bottom: 15px;
  width: 100%;
}

.additional__button {
  width: 100%;
}

.additional__criterion {
  text-align: start;
}

.additional__meaning {
  text-align: end;
}
