.page__body {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  min-width: 320px;
  margin: 0 auto;
}

@media (min-width: $tablet-width) {
  .page__body {
    min-width: $tablet-width;
    font-size: 16px;
    line-height: 24px;
  }
}

@media (min-width: $desktop-width) {
  .page__body {
    width: $desktop-width;
  }
}
